import React, { useState} from 'react'
import howToUseApp from './API/howToUseApp'

const About = () => {
      const [aboutData, setAboutData] = useState(howToUseApp);

    return (
       <>
       <section className="common-section our-services">
           <div className="container mb-5">
               <div className="row">
                   <div className="col-12 col-lg-5 text-center our-services-leftside-img">
                       <img src="./images/baap.jpg" alt="" />
                   </div>

                   {/* 1section rsd */}
                   <div className="col-12 col-lg-7 our-services-list">
                       <h3 className="mini-title">
                           --COURSES: Optionals
                       </h3>
                       <h1 className="main-heading">Optional Classroom</h1>
                       
                       {aboutData.map((curElem) => {
                           const { id, title, info } = curElem;
                           return (
                               <>
                                <div className="row our-services-info">
                           <div className="col-1 our-services-number">{id}</div>
                           <div className="col-10 our-services-data">
                               <h2>{title}</h2>
                               <p className="">
                                   {info}

                               </p>
                           </div>
                       </div>
                               </>
                           )
                       })}

                      
                       <br />
                       <button className="input-group-button">Learn More</button>
                   </div>
               </div>
           </div>

       </section>

       {/* 2nd Section */}

       <section className="common-section our-services our-services-rightside">
           <div className="container mb-5">
               <div className="row">
                   

                   {/* 1section rsd */}
                   <div className="col-12 col-lg-7 our-services-rightside-content d-flex justify-content-center align-items-start flex-column">
                       <h3 className="mini-title">
                           --COURSES: Optionals
                       </h3>
                       <h1 className="main-heading">Optional Classroom</h1>
                       
                       {aboutData.map((curElem) => {
                           const { id, title, info } = curElem;
                           return (
                               <>
                                <div className="row our-services-info">
                           <div className="col-1 our-services-number">{id}</div>
                           <div className="col-10 our-services-data">
                               <h2>{title}</h2>
                               <p className="">
                                   {info}

                               </p>
                           </div>
                       </div>
                               </>
                           )
                       })}

                      
                       <br />
                       <button className="input-group-button">Learn More</button>
                   </div>

                   <div className="col-12 col-lg-5 our-services-leftside-img">
                       <img src="./images/bapu.jpg" alt="" />
                   </div>
               </div>
           </div>

       </section>

       </>
    )
}

export default About
